@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  font-family: 'Inter', sans-serif;
}

#root {
  min-height: 100vh;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  max-width: 400px;
  width: 100%;
  margin: 0 auto;
}

input,
select {
  background: #FEF9F9;
  border: 1px solid #000000;
}

.form {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  width: 400px;
  max-width: 100%;
  min-height: 200px;
  border: 1px solid #000000;
  border-radius: 11px;
  padding: 5px 9px;

  &__title {
    font-weight: 400;
    font-size: 12px;
    margin-bottom: 10px;
  }

  &__elements {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 7px;
    margin-bottom: 8px;

    select {
      height: 35px;
      font-size: 12px;
    }

    @media all and (max-width: 360px) {
      align-items: flex-end;
      margin-bottom: 14px;
    }
  }

  &__icon {
    width: 30px;
    height: 30px;

    img {
      width: 100%;
    }
  }

  &__input-wrapper {
    display: flex;
    flex-direction: column;
    width: 219px;
    font-size: 10px;
    gap: 2px;
    position: relative;

    .input {
      width: 100%;
      height: 35px;
      padding-left: 10px;
    }

    .operator{
      @media all and (max-width: 360px) {
        position: absolute;
        top: 100%;
        width: 200px;
      }
    }
  }

  &__input-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__button {
    border: none;
    background: none;
    cursor: pointer;
    font-weight: 400;
    font-size: 12px;
    width: max-content;
  }
}

.clone-phone{
  font-size: 12px;
  margin: 5px 0;
  display: flex;
  justify-content: flex-start;
  gap: 10px;
}